export default function Settings() {
    return (
      <div className="space-y-6">
        <div className="bg-white p-6 rounded-lg shadow-md">
          <h3 className="text-xl font-semibold mb-4">Account Settings</h3>
          {/* Add settings fields like password change, notifications, etc. */}
          <div>
            <label className="text-gray-700">Change Password</label>
            <input
              type="password"
              className="mt-2 block w-full border border-gray-300 rounded-lg p-2"
              placeholder="Enter new password"
            />
          </div>
          <div>
            <label className="text-gray-700">Notifications</label>
            <div className="mt-2">
              <label className="inline-flex items-center">
                <input
                  type="checkbox"
                  className="form-checkbox text-blue-500"
                />
                <span className="ml-2 text-gray-700">Enable email notifications</span>
              </label>
            </div>
          </div>
        </div>
      </div>
    );
  }
  